    *{
        margin: 0;
        padding: 0;
    }
    .nei{
        padding: 10px;
    }
    .header_n{
        display: flex;
        align-items: center;
        margin-top: 10px;


    }
    .header_n h2{
        font-size: 18px;
        margin-left: 10px;
    }
    .bor_b{
        border-bottom:1px solid #f1f1f1; 
        margin: 30px 0
    }
    p{
