<template>
 <div class="detail">
     <header>
            <span class="iconfont icon-shangyiyehoutuifanhui" @click='chanp'></span>
           <span></span>
     </header>
     <section>
<div class="con">
    <h2><span>{{this.$store.state.obj.y}}</span></h2>
    <p></p>
    <p><span>{{this.$store.state.obj.day}}</span> | <span>{{this.$store.state.obj.day}}</span></p>
</div>
<div class="det">
    <h3></h3>
    <p> 
    +</p>
     <h4></h4>
     <p>1-3</p>
    <h4></h4>
    <p></p>
    <h4></h4>
    <p>100.00</p>
    <h4></h4>
    <p></p>
    <h4></h4>
    <p>100.00</p>
    <h4></h4>
    <p>1-3</p>
    <h4></h4>
    <p></p>
    <h4></h4>
    <p>0.00</p>
    <h4></h4>
    <p></p>
</div>
</section>
    <footer @click='out'>
        
    </footer>
 </div>
</template>
<script>
 export default {
     name:"Detail",
     methods:{
         chanp(){
             this.$router.push("/product")
         },
         out(){
            this.$router.push("/outmoney")
         }
     }
 }
</script>
<style scoped>
    
 .detail{
     font-size: 12px;
     display: flex;
     flex-direction: column;
     height: 100vh;
 }

 h3{
     text-align: center;
     font-weight: 800;
 }
 .detail h4{
     font-weight: 800;
 }
 .det>p{
     margin: 10px 0;
     text-indent: 2em;
 }
 header{
     height: 40px;
     color: #fff;
     font-size: 14px;
     text-align: center;
     line-height: 40px;
     position: absolute;
     background:#e94c4e;
     width: 100vw;
 }
 .iconfont{
        position: absolute;
        font-size: 20px;
        color: #fff;
        left: 0;
    }
 section{
     flex: 1;
     overflow: auto;
     margin-top: 40px;
 }
 .det{
     padding: 10px;
 }
 .con{
     background: #e94c4e;
     height: 150px;
     color: #fff;
     text-align: center;
     padding-top: 30px;
 }
 .con p{
     margin: 10px 0;
 }
 footer{
     height: 50px;
     color: #fff;
     font-size: 14px;
     text-align: center;
     line-height: 50px;
     background:#e94c4e;
     width: 100vw;
 }
</style>


